#red,
#green,
#blue {
  margin: 10px;
  display: inline-block;
  height: 200px;
}

#colorpicker {
  height: 240px;
  width: 310px;
  margin: 0 auto;
  padding: 10px;
  border: 1px solid #bfbfbf;
  background: #bfbfbf;
}

#result {
  margin: 60px 26px;
  height: 100px;
  width: 100px;
  display: inline-block;
  vertical-align: top;
  border: 1px solid #fff;
  box-shadow: 0 0 10px;
}

#red .noUi-connect {
  background: #c0392b;
}

#green .noUi-connect {
  background: #27ae60;
}

#blue .noUi-connect {
  background: #2980b9;
}
